@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@1,100&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Titillium+Web:wght@900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Titillium+Web:wght@900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Titillium+Web:wght@900&display=swap);
body {
  margin: 0;
  padding: 0;
  overflow-x: hidden;

  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  background-color: #1c0522;
  background-image: url(/portfolio/static/media/stars.fb95e3d1.png);
  background-repeat: repeat;
  object-fit: cover;
  padding: 0;
  margin: 0;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.bgColor {
  background-image: linear-gradient(#202040, #543864, #ff6363, #ffbd69);
}
.section {
  position: relative;
  height: 95vh;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
}
.section::before {
  content: "";
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 300px;
  z-index: 1000;
  background-image: linear-gradient(to top, #1c0522, transparent);
}
.section img {
  max-height: 97vh;
  position: absolute;
  height: 100vh;
  width: 100%;
  object-fit: cover;
}

.moon {
  mix-blend-mode: screen;
  z-index: 9;
}

.frontM {
  z-index: 10;
}
.text {
  position: absolute;
  font-family: "Titillium Web", sans-serif;
  letter-spacing: 7px;
  color: peachpuff;
  white-space: nowrap;
  font-size: 15vh;
  z-index: 9;
  -webkit-transform: translateY(50%);
          transform: translateY(50%);
}

.textScroll {
  position: absolute;
  font-family: "Titillium Web", sans-serif;
  letter-spacing: 7px;
  color: white;
  white-space: nowrap;
  font-size: 5vh;
  z-index: 9;
}

.arrowD {
  -webkit-transform: translateY(180px);
          transform: translateY(180px);
  -webkit-transform: translateX(110px);
          transform: translateX(110px);
}

li {
  list-style: none;
  margin-left: 20px;
}

.appBar {
  background-color: transparent;
  z-index: 15;
  position: absolute;
  top: 0;
  left: 0;
  width: 95%;
  padding: 10px 60px;
  display: flex;
  margin: 2;
  justify-content: space-between;
  align-items: center;
}
a {
  text-decoration: none;
  padding: 6px 15px;
  color: white;
  border-radius: 20px;
  font-size: large;
  text-transform: uppercase;
  letter-spacing: 2px;
}

.appBar ul li a:hover {
  background-color: white;
  color: #1c0522;
}

.sec2 {
  position: relative;
  margin: auto;
  margin-top: 100px;
  padding: 50px;
  z-index: 11;
  height: 40%;
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  overflow: hidden;
}

.headers {
  font-family: "Titillium Web", sans-serif;
  font-size: 10vh;
  letter-spacing: 5px;
  color: #f3b356;
}

.body {
  font-size: 5vh;
  color: white;
}
.image {
  position: absolute;
  -webkit-transform: translateY(200px);
          transform: translateY(200px);
  right: 300px;
  object-fit: cover;
  margin: 20px;
}
.image2 {
  position: absolute;
  -webkit-transform: translateY(30px);
          transform: translateY(30px);
  left: 150px;
  object-fit: cover;
  margin: 20px;
}
.section2 {
  position: relative;
  height: 95vh;
  width: 100%;
  padding: 10px;
  display: flex;
  justify-content: center;
  overflow: hidden;
}

#skillHead {
  font-family: "Titillium Web", sans-serif;
}

